function sliderPurchase() {
    var swiper = new Swiper('.purchase .swiper', {
        slidesPerView: 4,
        spaceBetween: 56,
        navigation: {
            nextEl: '.purchase .swiper-button-next',
            prevEl: '.purchase .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 25,
            },
            577: {
                slidesPerView: 2,
                spaceBetween: 25,
            },
            769: {
                slidesPerView: 3,
                spaceBetween: 25,
            },
            993: {
                slidesPerView: 4,
                spaceBetween: 25,
            },
            1401: {
                spaceBetween: 56,
            }
        }
    })
}

