function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        slidesPerView: 3,
        spaceBetween: 35,
        freeMode: true,
        watchSlidesProgress: true,
        direction: "horizontal",
        breakpoints: {
            320: {
                spaceBetween: 20,
            },
            577: {
                spaceBetween: 35,
            }
        }
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 10,
        slidesPerView: 1,
        thumbs: {
            swiper: swiper,
        },
        navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev",
        },
    });
}

