$(document).ready(function() {

    sliderPurchase();
    sliderProduct();
    gallery();
    sliderArticle();
    sliderArticle2();
    sliderImages();
    sliderReviews();


    // Burger - Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-bottom__menu').toggleClass('header-bottom__menu--active')
        $('body').toggleClass('fixed-body')
    })


    // Hover - Click
    $(".header-bottom__item").hover(onIn, onOut);
    $(".header-bottom__item .header-bottom__item-show").click(function () {
        if ($(this).parents(".header-bottom__item").hasClass("header-bottom__item--active")) {
            $(this).parents(".header-bottom__item").removeClass("header-bottom__item--active")
        } else {
            $(this).parents(".header-bottom__item").addClass("header-bottom__item--active")
        }
    })

    // Hover - Click2
    $(".header-bottom__question").hover(onIn2, onOut2);
    $(".header-bottom__question .header-bottom__question-show").click(function () {
        if ($(this).parents(".header-bottom__question").hasClass("header-bottom__question--active")) {
            $(this).parents(".header-bottom__question").removeClass("header-bottom__question--active")
        } else {
            $(this).parents(".header-bottom__question").addClass("header-bottom__question--active")
        }
    })

    // Search
    $('.search-btn').click(function () {
        $(this).toggleClass('search--active')
        $('.search').toggleClass('search--active')
        $('body').toggleClass('fixed-body')
    })

        $('.search-close').click(function () {
            $('.search-btn').removeClass('search--active')
            $('.search').removeClass('search--active')
            $('body').removeClass('fixed-body')
        })

    // Select
    $(".custom_select select").niceSelect()

    // Input-number
    $(".input-number").on('input', function(e){
        this.value = this.value.replace(/[^0-9\.]/g, '');
    });

    //Accordion
    $(".catalog__item-show").click((function(){$(this).toggleClass("catalog__item-show--active")
    $(this).siblings(".catalog__item-hidden").slideToggle()}));

    // Tabs
    $(".sort").click(function () {
        let path = $(this).attr("data-tab2-path")
        $(this).siblings(".sort").removeClass("sort--active")
        $(this).addClass("sort--active")

        $(this).parents(".catalog__inner").find(".catalog__content").removeClass("catalog__content--active")
        $(this).parents(".catalog__inner").find(`.catalog__content[data-content2-path="${path}"]`).addClass("catalog__content--active")
    })

    // Tabs2
    $(".catalog__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".catalog__tab").removeClass("catalog__tab--active")
        $(this).addClass("catalog__tab--active")

        $(this).parents(".catalog__inner").find(".catalog__cards").removeClass("catalog__cards--active")
        $(this).parents(".catalog__inner").find(`.catalog__cards[data-content-path="${path}"]`).addClass("catalog__cards--active")
    })

    // Favorite
    $('.product-card2__favorite').click(function () {
        $(this).toggleClass('product-card2__favorite--active')
    })

    // modal

    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }


})

function onIn() {
    if (window.innerWidth > 992) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header-bottom__item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 992) {
        $(this).removeClass("header-bottom__item--active")
    }
}

